<template>
  <!-- 白名单页面 -->
  <div class="whitelist">
    <!-- 轮播图-banner -->
    <div class="block hidden-md-and-down">
      <el-carousel height="34rem">
        <el-carousel-item v-for="item in img" :key="item.id">
          <el-image :src="item.imgurl" class="image" fit="cover"></el-image>
        </el-carousel-item>
      </el-carousel>
      <!-- news-新闻 title="News" -->
      <el-alert type="info" show-icon :closable="false">
        <p @click="target(0)">{{listNews.title}}</p><!--todo 这里注意，target（）参数可变-->
      </el-alert>
    </div>

    <el-card class="whitelist-card">
      <div slot="header" class="clearfix">
        <span class="card-title">{{ $t('whitelist.cardTilte') }}</span>
      </div>
      <div style="text-align: center">
        <div class="body-title">
          <p class="title-countdown">{{ $t('whitelist.countdown') }}</p>
          <p class="countdown-time"
          >{{ countDown.d }} Days {{ countDown.h }} : {{ countDown.m }} : {{ countDown.s }}</p>
          <p class="whitelist-rules">{{ $t('whitelist.rules') }}</p>
        </div>
        <div class="card-middle">
          <el-button
            round
            @click="toBeWhitelistAddress"
            :disabled="whiteStatus"
          >{{ whiteStatus ? $t('whitelist.disableBtn') : $t('whitelist.btnSilver')}}</el-button>
        </div>
      </div>
    </el-card>

    <el-card class="box-card" :body-style="{ padding: '0px',height: '305px'}">
      <div slot="header" class="clearfix">
        <span class="card-title">{{ $t('commom.address') }}</span>
      </div>
      <div style="height: 250px;">
      <div v-for="address in whiteList.slice((whitePage.currentPage - 1) * whitePage.pageSize, whitePage.currentPage * whitePage.pageSize)" :key="address.index" class="address">{{address}}</div>
      </div>
      <!-- 分页按钮 -->
      <div style="text-align: center;padding: 10px 0;border-top: 1px solid #f9d76c;">
        <el-pagination
          layout="prev, pager, next"
          :total="whiteList.length"
          :page-size="whitePage.pageSize"
          :pager-count="5"
          @current-change="whitePageCurrentChange"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import { checkUrlParam } from '../Mixin/checkUrlParam'
import store from '../store/index'
import {metaMaskInfo} from '../Mixin/metaMaskInfo'
import listZh from '../static/news-zh'
import listEn from '../static/news-en'

export default {
  name: 'WhiteList',
  mixins: [checkUrlParam, metaMaskInfo],
  data () {
    return {
      whitePage: { // 白名单分页
        pageSize: 10, // 分页，单页展示的条目数
        currentPage: 1 // 分页， 当前页的页码
      },
      img: [
        { id: 0, imgurl: require('../assets/img/purchase1.jpg') },
        { id: 1, imgurl: require('../assets/img/purchase3.jpg') }
      ],
      isListen: false // 标志，是否开启tobeWhiteList监听
    }
  },
  computed: {
    // news 处理
    listNews () {
      // 返回最新的三条新闻，即最上面的三条新闻
      if (this.$i18n.locale === 'en') {
        return listEn[0]
      }
      return listZh[0]
    },
    countDown () {
      const d = this.$store.state.countDown.days
      const h = this.$store.state.countDown.hours
      const m = this.$store.state.countDown.minutes
      const s = this.$store.state.countDown.seconds
      return {
        d, h, m, s
      }
    },
    whiteStatus () {
      const status = this.$store.state.metamask.whiteStatus
      if (status === null) {
        return false
      }
      return status
    },
    whiteList () { // todo 已注入页面
      let allWhiteList = this.$store.state.contract.allWhiteList
      if (allWhiteList === null) {
        allWhiteList = []
      }
      return allWhiteList
    }
  },
  watch: {
    whiteList: function (newVal) {
      if (newVal !== null && !this.$ethConf.subscribeMark.TobeWhitelistAddress) { // 防止重复订阅，使用全局标志位解决; > 0 是否为了防止事件触发的同时，return 白名单列表,修改为不为空
        const contractTeam = this.$myContracts && this.$myContracts.contractTeam
        if (contractTeam) {
          this.$store.dispatch('contract/startListenEvent', { contract: contractTeam, eventName: 'TobeWhitelistAddress' })
        }
      }
    }
  },
  methods: {
    // news处理
    target (index) {
      this.$store.commit('news/selectIndex', index)
      this.$router.push('/news/newsDetail')
    },
    whitePageCurrentChange (val) { // 当前的页码，当页码改变时回调
      // 页面渲染成功，从结果倒推，渲染的顺序应该是，切换页码，执行currentChange方法，然后执行v-for进行页面渲染
      this.whitePage.currentPage = val
    },
    async toBeWhitelistAddress () {
      // 判断用户是否已经是在白名单列表
      const whiteStatus = this.whiteStatus
      if (whiteStatus) { // 是白名单则不可点击
        return
      }
      const data = {}
      data.referralAddress = this.checkUrlParam()
      // 判断推荐地址是不是admin地址，
      const adminAddress = this.$ethConf.adminAddress

      if (!data.referralAddress || !adminAddress.includes(data.referralAddress)) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.checkAddress,
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        this.$notify({
          title: this.metaInfo.warning,
          message: this.metaInfo.loginWallet,
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) {
          this.$notify({
            title: this.metaInfo.warning,
            message: this.metaInfo.networkVersion,
            type: 'warning'
          })
        } else {
          const contractTeam = this.$myContracts && this.$myContracts.contractTeam // TeamReward合约地址
          const myWeb3 = this.$myWeb3
          data.gasPrice = await myWeb3.eth.getGasPrice()
          data.gasLimit = this.$ethConf.gasLimit
          if (contractTeam) {
            data.from = myProvider.selectedAddress
            contractTeam.methods.toBeWhitelistAddress(data.referralAddress).send({
              from: data.from,
              gasPrice: data.gasPrice,
              gas: data.gasLimit
            }, function (error, result) {
              if (error) {
                console.error(error)
              } else {
                // 更改白名单状态
                store.commit('metamask/setWhiteStatus', true) // 在回调函数中，无法获取当前组件的this对象，箭头函数应该可以，和setTimeOut应该类似
              }
            })
          }
        }
      }
    }
  },
  created () {
    // 查询 白名单列表

    // this.$store.commit('metamask/setWhiteStatus', true) todo 这里为什么会有这个，难道是自己在测试disabled的时候使用的？
    const myProvider = this.$myProvider // 只要安装了metamask就可以看到
    if (myProvider) { // 防止重复加载, 如果使用订阅tobeWhiteList
      const allWhiteList = this.$store.state.contract.allWhiteList
      if (allWhiteList === null) {
        this.$store.dispatch('contract/getAdminWhitelistAddress')
      }
    }
    let strAddress = this.$route.query.to
    if (strAddress) {
      this.$notify({
        title: this.metaInfo.infoAddress,
        message: strAddress.slice(0, 6) + '...' + strAddress.slice(36, 42),
        type: 'warning',
        duration: 0,
        position: 'bottom-left'
      })
    }
  }
  // todo whiteStatus的获取是异步，所以，可能会出现whiteStatus值没有返回就进入了本页面！测试如果出现问题，则延迟进入页面
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.whitelist {
  .whitelist-card {
    .body-title {
      .title-countdown {
        margin: 10px 0;
        font-size: 37px;
        font-family: ArialMT;
        font-weight: bold;
        font-style: italic;
        color: #d4bb7c;
      }
      .countdown-time {
        margin: 5px 0;
        font-size: 28px;
        font-family: ArialMT;
        font-weight: 400;
        color: #d4bb7c;
      }
      .whitelist-rules {
        font-size: 18px;
        font-family: ArialMT;
        font-weight: 400;
        color: #ddc597;
        line-height: 35px;
        margin: 35px 0;
        text-align: left;
        text-indent: 40px;
      }
    }
    .card-middle {
      .el-button {
        font-size: 20px;
        width: 260px;
        background-color: $card-subhead;
        border-radius: 20px;
        border-width: inherit;
        font-family: ArialMT;
        font-weight: bold;
        color: #000000;
        word-break: break-all;
      }
    }
  }
  .address {
    text-align: center;
    margin: 10px 0;
    font-size: 13px;
  }
}
</style>
<style scoped>
.el-card {
  margin-top: 1.5rem;
  background-color: #252323;
  border: 0px solid #252323;
  color: #dac28f;
}
.box-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #efdbca, #fdd650);
  font-size: 16px;
  border: 0px solid #f9d76c;
}
.form-card >>> .el-card__header {
  padding: 8px 20px;
  background: linear-gradient(to bottom, #2c2c2c, #242121);
  color: #f9d76c;
  font-size: 16px;
  border: 0px solid #f9d76c;
}
/* 分页 */
.el-pagination {
  background-color: #252323;
}

.el-pagination button:disabled {
  color: #c0c4cc;
  background-color: #252323;
}

.el-pagination button {
  color: #c0c4cc;
  background-color: #252323;
}

.el-pagination .btn-prev,
.el-pagination .btn-next {
  background-size: 16px;
  background-color: #252323;
  color: #c0c4cc;
}

.el-pager li {
  background: #252323;
  color: #c0c4cc;
}
</style>



